
@media screen and (min-width: 750px) {
body {
    font-size: 125%;
}
}
body {
  background: url("//www.toptal.com/designers/subtlepatterns/patterns/cheap_diagonal_fabric.png") repeat;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.small-border {
  width: 100px;
  height: 3px;
  margin: 0 auto;
  background-color: #c0c0c0; /* #2c3e50; */
}



